   License: none (public domain)
*/
@import url(css_reset.css);

body {
    font-family: "Roboto" , "Microsoft JhengHei";
    font-style: italic;
}

img {
  display: block;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

.header {
    height: 100vh;
    background: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week2/banner.png) center center;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.nameCard {
    & li {
        color: #FFF;
    }
    & li.text > p {
        font-size: 24px;
        line-height: 42px;
    } 
    & li.name > h1 {
        font-size: 80px;
        line-height: 102px;
    } 
    & li.jobTitle {
        padding-top: 32px;
    }
    & li.jobTitle > p {
        font-size: 24px;
        line-height: 36px;
    }
    & .socialImg {
        display: flex;
        padding-top: 40px;
        justify-content: space-between;
    }
}
 

.about {
    display: flex;
    justify-content: center;
    align-content: center;
}

.aboutCard {
    text-align: center;
    & h2 {
        padding-top: 56px;
        padding-bottom: 40px;
        font-weight: 400;
        font-size: 64px;
        line-height: 84px;
    }
    
    & img {
        padding-bottom: 20px;
        margin: 0 auto;
    }
    
    
    & ul li p {
        font-size: 24px;
        line-height: 42px;  
        padding-bottom: 4px;
    }
    
    & ul li.textBottom p{
        padding-top: 4px;
        padding-bottom: 4px;
        line-height: 36px;
    }
}


.workExperience {
    margin-top: 20px;
    padding-bottom: 56px;
    border-top: 1px solid #000000;
    & li {
        display: flex;
        align-items: baseline;
        justify-content: center;
        padding-top: 28px;
        & .listIcon {
            background-color: #891818;
            height: 22px;
            width: 22px;
            border-radius: 50%;
            margin-right: 16px;
        }
    }
}


li.contentTitle > h2 {
    font-size: 64px;
    line-height: 84px;
    padding-top: 56px;
}

.brand {
    background-color: #000000;
    color:#FFF;
    display: flex;
    justify-content: center;
    padding-bottom: 120px;

    & > ul li {
        display: flex;
        justify-content: center;
    }
    & > ul li > p {
        font-size: 24px;
        line-height: 42px;
        padding-top: 12px;
    }
    & > ul li.brandDetails > p {
        text-align: center;
        font-size: 20px;
        line-height: 30px;
        padding-top: 32px;
        padding-bottom: 68px;
    }
    & > ul li.brandImage {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }
    & > ul li.brandImage img {
        padding-bottom: 40px;
    }
}

.works {
    padding-bottom: 96px;
    & ul > li { 
        display: flex;
        justify-content: space-evenly;
    }
    & ul > li h2,p{ 
        margin: 0 auto;
    }
    & ul > li.workDetails p {
        font-size: 20px;
        line-height: 30px;
        padding-top: 16px;
        padding-bottom: 56px;
    }
    & ul > li.workImage {
        flex-wrap: wrap;
    }
    & ul > li.workImage .workCard{
        display: flex;
        flex-direction: column;
        & p {
            font-size: 20px;
            line-height: 42px;
            padding-top: 16px;
            padding-bottom: 48px;
            margin: 0 auto;
        }
    }
}

.footer {
    background: #000000;
    color: white;
    padding-bottom: 48px;
    & ul {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    & li > h2 {
        font-size: 64px;
        line-height: 84px;
        padding-top: 64px;
        padding-bottom: 16px;
    }
    & li > p {
        font-size: 24px;
        line-height: 36px;
        padding-bottom: 16px;
        font-weight: 500;
        & a {
            text-decoration: none;
            color: #FFF;
        }
    }
}
